export default {
  name: "allOrder",
  data() {
    return {
      current_text: '全部订单',
      currentTab: 0,
      showSelected: false,
      list: ['我的订单', '我的追号', '我的中奖', '待开奖', '我的撤单']
    }
  },
  methods: {
    toChoose(item, index) {
      this.current_text = item
      this.currentTab = index
      this.showSelected = false
    },
    toShowSelect() {
      this.showSelected = true
    },
  }
}



