import {
    RED_WAVE,
    BLUE_WAVE,
    GREEN_WAVE
} from "@/config/config";
export default {
    name: "home",
    data() {
        return {
            numberList:['一','二','三','四','五','六','特码'],
            list:[],//列表
            isScroll:false,
            headHeight: 30,
            bodyHeight: window.innerHeight - 30,
        }
    },
    methods: {
        //获取最近7天的开奖结果
        hkGameResult() {
            this.$Api(
                {
                    api_name: "kkl.user.getLastSevenListResult",
                    is_app: 1
                },
                (err, data) => {
                    if (!err) {
                        this.list = data.data
                        let resultArr = [];
                        this.list.forEach(item=> {
                            item.result.split(",").forEach(item => {
                                let bg;
                                if (RED_WAVE.indexOf(+item) !== -1) {
                                    bg = 1;
                                }
                                if (BLUE_WAVE.indexOf(+item) !== -1) {
                                    bg = 2;
                                }
                                if (GREEN_WAVE.indexOf(+item) !== -1) {
                                    bg = 3;
                                }
                                resultArr.push({
                                    number: item,
                                    bg
                                });
                            })
                            item.result = resultArr
                        })
                        console.log(this.list)
                    }
                })
        },
        handleScroll(){
            this.$nextTick(() => {
                let scrollLeft = this.$refs.box.scrollLeft
                let scrollTop =  window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
                console.log(scrollTop)
                if(scrollLeft > 0){
                    //滑动了
                    this.isScroll = true
                }else{
                    this.isScroll = false
                }
            });

        }
    },
    mounted() {
        // window.addEventListener('scroll', this.handleScroll, true)
        this.hkGameResult()
    }

}



